* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  display: inline-block;
}
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}
.container {
  max-width: 117rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}
body {
  font-family: 'PT Serif', serif;
  font-weight: 400;
  line-height: 18px;
  color: #5e5e5e;
  font-size: 1.4rem;
  overflow-x: hidden;
}
.default-btn {
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: #fff;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
  padding: 0.8rem 3rem 0.8rem 2.4rem;
  border: 1px solid #ffc222;
  display: inline-block;
  border-right-width: 2rem;
  position: relative;
  z-index: 2;
}
.default-btn::after {
  content: "\f105";
  font-family: 'icomoon';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -1.2rem;
}
.default-btn:focus {
  outline: none;
}
.to-top {
  position: fixed;
  bottom: 5%;
  right: 2%;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #ffc222;
  cursor: pointer;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.4);
}
.to-top .icon-angle-right {
  font-size: 2rem;
  color: #fff;
  transform: rotate(-90deg);
}
.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  padding: 1rem;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.4);
  align-items: center;
}
::-webkit-scrollbar {
  width: 4px !important;
}
::-webkit-scrollbar-thumb {
  background-color: #ffc222;
  border-radius: 50px !important;
}
.header {
  font-family: 'Roboto', sans-serif;
}
.header__top {
  background-color: #303030;
}
.header__btn {
  float: right;
  line-height: 3.6rem;
  color: #1b1b1b;
  background-color: #ffc222;
  font-size: 1.6rem;
  padding: 0.5rem 2rem 0.4rem 5rem;
  position: relative;
}
.header__btn::before {
  content: "\f0b1";
  font-family: 'icomoon';
  position: absolute;
  left: 2.2rem;
  color: #1b1b1b;
}
@media (max-width: 25em) {
  .header__btn::before {
    display: block !important;
  }
}
@media (max-width: 46.875em) {
  .header__btn::before {
    display: none;
  }
}
@media (max-width: 46.875em) {
  .header__btn {
    float: none !important;
    width: 100%;
  }
}
.header__contacts::after {
  content: "";
  clear: both;
  display: block;
}
@media (max-width: 46.875em) {
  .header__contacts {
    text-align: center;
  }
}
.header__phone,
.header__email {
  color: #cbcacb;
  letter-spacing: 0.25px;
  line-height: 4.5rem;
  position: relative;
}
.header__phone {
  padding: 0 3rem 0 2rem;
}
.header__phone::before {
  content: "\f095";
  font-family: 'icomoon';
  position: absolute;
  left: 0;
  color: #ffc222;
}
.header__email {
  padding-left: 2.4rem;
}
@media (max-width: 23.4375em) {
  .header__email {
    padding-right: 30px;
  }
}
.header__email::before {
  content: "\f003";
  font-family: 'icomoon';
  position: absolute;
  left: 0;
  color: #ffc222;
}
.header__content {
  padding: 3.4rem 0 2.3rem;
}
.header__content-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__btn-menu {
  display: none;
  font-size: 2.8rem;
  cursor: pointer;
  color: #ffc222;
}
@media (max-width: 46.875em) {
  .header__btn-menu {
    display: block;
  }
}
@media (max-width: 46.875em) {
  .menu ul {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    margin-top: 2rem;
    z-index: 50;
  }
}
.menu ul li {
  display: inline-block;
}
@media (max-width: 46.875em) {
  .menu ul li {
    display: block;
    text-align: center;
  }
}
.menu ul li a {
  font-size: 1.6rem;
  color: #323232;
  line-height: 3.6rem;
  border: 1px solid transparent;
  padding: 0 1.6rem;
  transition: all 0.5s;
}
.menu ul li a:hover {
  border-color: #ffc222;
}
@media (max-width: 46.875em) {
  .menu ul li a:hover {
    background-color: #303030;
    color: #fff;
  }
}
@media (max-width: 46.875em) {
  .menu ul li a {
    display: block;
    background-color: #ffc222;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    padding: 0 !important;
  }
}
.menu li + li {
  padding-left: 1rem;
}
@media (max-width: 46.875em) {
  .menu li + li {
    padding-left: 0;
  }
}
#modal {
  display: none;
  width: 30rem;
}
#modal input {
  display: block;
  width: 100%;
  outline: none;
  margin-bottom: 2rem;
  height: 4rem;
  border: 1px solid #e6e6e6;
  font-family: 'Roboto', sans-serif;
  padding-left: 1rem;
  font-weight: 400;
}
#modal input[type="submit"] {
  background-color: #ffc222;
  border: none;
}
.slider {
  background-image: url("../img/slider-fon.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 50rem;
}
.slider__inner {
  position: relative;
}
.slider__item-content {
  padding-top: 11rem;
  max-width: 66.5rem;
  text-align: center;
  margin: 0 auto;
}
.slider__item {
  outline: none;
}
.slider__title {
  text-transform: uppercase;
  font-size: 3rem;
  line-height: 3.6rem;
  color: #fff;
  font-weight: 500;
  margin-bottom: 2.2rem;
  letter-spacing: 1.5px;
  font-family: 'Roboto', sans-serif;
}
@media (max-width: 25.875em) {
  .slider__title {
    font-size: 2.4rem;
  }
}
.slider__text {
  font-size: 1.8rem;
  color: #d3d2d2;
  margin-bottom: 5rem;
  line-height: 2.4rem;
}
@media (max-width: 25.875em) {
  .slider__text {
    font-size: 1.6rem;
  }
}
@media (max-width: 56.25em) {
  .slider .slick-btn {
    display: none !important;
  }
}
.slick-btn {
  position: absolute;
  top: 70%;
  z-index: 3;
  width: 4rem;
  height: 4rem;
  border: 1px solid #fff;
  background-color: #ffc222;
  color: #fff;
  cursor: pointer;
}
.slick-btn::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.slick-btn:focus {
  outline: none;
}
.slick-next {
  right: 0;
}
.slick-next::before {
  content: "\f105";
  font-family: 'icomoon';
}
.slick-prev {
  left: 0;
}
.slick-prev::before {
  content: "\f104";
  font-family: 'icomoon';
}
.slick-btn.slick-disabled {
  cursor: default;
  background-color: transparent;
}
.services {
  padding: 5rem 0 3rem;
}
.services__top,
.news__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6rem;
}
@media (max-width: 56.25em) {
  .services__top,
  .news__top {
    display: block;
  }
}
.services__title-box,
.form__title-box,
.news__title-box {
  max-width: 52rem;
  padding-left: 14rem;
