}
.services__title-box::before,
.form__title-box::before,
.news__title-box::before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 10rem;
  height: 0.2rem;
  background-color: #ffc222;
  left: 0;
  top: 1.1rem;
}
@media (max-width: 56.25em) {
  .services__title-box::before,
  .form__title-box::before,
  .news__title-box::before {
    display: none;
  }
}
@media (max-width: 56.25em) {
  .services__title-box,
  .form__title-box,
  .news__title-box {
    padding-left: 0;
    text-align: center;
    margin: 0 auto 3rem;
